import {
	serverUrl
} from '../config/index.js'

function formatTime(time) {
	if (typeof time !== 'number' || time < 0) {
		return time
	}

	var hour = parseInt(time / 3600)
	time = time % 3600
	var minute = parseInt(time / 60)
	time = time % 60
	var second = time

	return ([hour, minute, second]).map(function(n) {
		n = n.toString()
		return n[1] ? n : '0' + n
	}).join(':')
}

function formatLocation(longitude, latitude) {
	if (typeof longitude === 'string' && typeof latitude === 'string') {
		longitude = parseFloat(longitude)
		latitude = parseFloat(latitude)
	}

	longitude = longitude.toFixed(2)
	latitude = latitude.toFixed(2)

	return {
		longitude: longitude.toString().split('.'),
		latitude: latitude.toString().split('.')
	}
}
var dateUtils = {
	UNITS: {
		'': 31557600000,
		'': 2629800000,
		'': 86400000,
		'': 3600000,
		'': 60000,
		'': 1000
	},
	humanize: function(milliseconds) {
		var humanize = '';
		for (var key in this.UNITS) {
			if (milliseconds >= this.UNITS[key]) {
				humanize = Math.floor(milliseconds / this.UNITS[key]) + key + '';
				break;
			}
		}
		return humanize || '';
	},
	format: function(dateStr) {
		var date = this.parse(dateStr)
		var diff = Date.now() - date.getTime();
		if (diff < this.UNITS['']) {
			return this.humanize(diff);
		}
		var _format = function(number) {
			return (number < 10 ? ('0' + number) : number);
		};
		return date.getFullYear() + '/' + _format(date.getMonth() + 1) + '/' + _format(date.getDay()) + '-' +
			_format(date.getHours()) + ':' + _format(date.getMinutes());
	},
	parse: function(str) { //"yyyy-mm-dd HH:MM:ss"Date
		var a = str.split(/[^0-9]/);
		return new Date(a[0], a[1] - 1, a[2], a[3], a[4], a[5]);
	},
	dateFormat: function(dateStr) {
		var date = new Date(dateStr);
		var _format = function(number) {
			return (number < 10 ? ('0' + number) : number);
		};
		return date.getFullYear() + '-' + _format(date.getMonth() + 1) + '-' + _format(date.getDay());
	}
};

function substr(v) {
	if (v != "" & v != undefined & v != null) {
		if (v.substr(v.length - 1, 1) == "," || v.substr(v.length - 1, 1) == ";") {
			return v.substr(0, v.length - 1);
		}
		return v
	}
}

function subFilter(v, len) {
	if (v != "" & v != undefined & v != null) {
		if (v.length > len) {
			return v.substr(0, len);
		} else {
			return v;
		}
	}
}

function request(param) {
	uni.showLoading({
		title: ''
	});
	uni.request({
		url: serverUrl + param.url,
		data: param.contentType ? param.param : JSON.stringify(param.param),
		method: 'POST',
		header: {
			'content-type': param.contentType ? param.contentType : 'application/json',
			'token': uni.getStorageSync('Token') ? uni.getStorageSync('Token') : ''
		},
		dateType: "json",
		success: (res) => {
			uni.hideLoading();
			if (res == null) {
				res = {};
			}
			if (typeof res == "string") {
				res = JSON.parse(res);
			}
			if (typeof(param.success) == 'function') {
				if (res.data.errorCode == "SUCCESS") {
					param.success(res.data);
				} else if (res.data.errorCode == "E003") {
					uni.showModal({
						title: '',
						content: '',
						success: function(res) {
							if (res.confirm) {
								uni.switchTab({
									url: '/pages/center/center'
								});
							} else if (res.cancel) {
								console.log('');
							}
						}
					});
				} else {
					param.error(res.data);
					uni.showToast({
						title: res.data.message,
						icon: 'none',
						duration: 2000
					});
				}
			} else {
				uni.showToast({
					title: '',
					icon: 'none',
					duration: 2000
				});
			}

		},
		fail: (res) => {
			uni.hideLoading();
			uni.showToast({
				title: '',
				icon: 'none',
				duration: 2000
			});
		}
	});
}

