function IdentityCodeValid(code) {
	var city = {
		11: "",
		12: "",
		13: "",
		14: "",
		15: "",
		21: "",
		22: "",
		23: " ",
		31: "",
		32: "",
		33: "",
		34: "",
		35: "",
		36: "",
		37: "",
		41: "",
		42: " ",
		43: "",
		44: "",
		45: "",
		46: "",
		50: "",
		51: "",
		52: "",
		53: "",
		54: " ",
		61: "",
		62: "",
		63: "",
		64: "",
		65: "",
		71: "",
		81: "",
		82: "",
		91: " "
	};
	var tip = "";
	var pass = true;
	//6831
	if (!code || !/^\d{6}(18|19|20)?\d{2}(0[1-9]|1[12])(0[1-9]|[12]\d|3[01])\d{3}(\d|X)$/i.test(code)) {
		tip = "";
		pass = false;
	} else if (!city[code.substr(0, 2)]) {
		tip = "";
		pass = false;
	} else {
		//18
		if (code.length == 18) {
			code = code.split('');
			//(aiWi)(mod 11)
			//
			var factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];
			//
			var parity = [1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2];
			var sum = 0;
			var ai = 0;
			var wi = 0;
			for (var i = 0; i < 17; i++) {
				ai = code[i];
				wi = factor[i];
				sum += ai * wi;
			}
			var last = parity[sum % 11];
			if (parity[sum % 11] != code[17]) {
				tip = "";
				pass = false;
			}
		}
	}
	if (!pass) {
		uni.showToast({
			title: tip,
			icon: 'none',
			duration: 2000
		});
	};
	return pass;
}

function random() {
	var num = '';
	for (var i = 0; i < 3; i++) {
		num += Math.floor(Math.random() * 10);
	}
	return num
}

function uuid() {
	var s = [];
	var hexDigits = "0123456789abcdef";
	for (var i = 0; i < 32; i++) {
		s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
	}
	s[12] = "4"; // bits 12-15 of the time_hi_and_version field to 0010
	s[17] = hexDigits.substr((s[17] & 0x3) | 0x8, 1); // bits 6-7 of the clock_seq_hi_and_reserved to 01
	// s[8] = s[13] = s[18] = s[23] = "-";

	var uuid = s.join("");
	return uuid;
}

function updateMessage(msg, hasRead) {
	var fromUid = '';
	if(hasRead == '0'){
		fromUid = msg.fromUid;
	}else{
		fromUid = msg.toUid;
	}
	//
	console.log('send msg');
	console.log(msg.msg.content);
	var messageList = uni.getStorageSync('messageList');
	if (null != messageList) {
		// 
		for (var i = 0; i < messageList.length; i++) {
			if (messageList[i].openId == fromUid) {
				messageList[i].message = msg.msg.content;
				messageList[i].time = msg.time;
				messageList[i].count = messageList[i].count + 1;
			}
		}
	} else {
		// 
		var message = {
			title: msg.username,
			openId: fromUid,
			avatarUrl: msg.face,
			message: msg.msg.content,
			time: msg.time,
			count: 1,
			type: 2 //
		};
		messageList = [];
		messageList.push(message);
	}
	uni.setStorageSync('messageList', messageList);
	//
	msg.hasRead = hasRead; //
	var messageDetail = uni.getStorageSync("messageDetail");
	var messageDetailList = messageDetail[fromUid];
	if (messageList) {
		messageDetailList.push(msg);
	} else {
		messageDetailList = [];
		messageDetailList.push(msg);
		messageDetail[fromUid] = messageDetailList;
	}
	uni.setStorageSync("messageDetail", messageDetail);
}

